/**
 * Script de test de la connexion Supabase
 * Ex√©cutez ce fichier dans la console du navigateur pour v√©rifier :
 * 1. La connexion √† Supabase
 * 2. L'existence de la table user_scores
 * 3. Les permissions
 */

import { createClient } from '@/lib/supabase/client';

export async function testSupabaseConnection() {
    console.log('üîç Test de connexion Supabase...');

    const supabase = createClient();

    // Test 1: V√©rifier que le client existe
    console.log('‚úÖ Client Supabase cr√©√©');

    // Test 2: V√©rifier la table user_scores existe
    console.log('\nüìä Test de la table user_scores...');

    const { data, error } = await supabase
        .from('user_scores')
        .select('*')
        .limit(1);

    if (error) {
        console.error('‚ùå Erreur lors de l\'acc√®s √† la table:', error);

        if (error.code === '42P01') {
            console.error('\n‚ö†Ô∏è LA TABLE "user_scores" N\'EXISTE PAS !');
            console.log('\nüìù SOLUTION :');
            console.log('1. Allez sur https://supabase.com/dashboard');
            console.log('2. S√©lectionnez votre projet');
            console.log('3. Allez dans "SQL Editor"');
            console.log('4. Copiez le contenu de "supabase_migration.sql"');
            console.log('5. Collez et ex√©cutez le script');
        } else if (error.code === 'PGRST301') {
            console.error('\n‚ö†Ô∏è PROBL√àME DE PERMISSIONS (RLS) !');
            console.log('\nüìù SOLUTION :');
            console.log('1. V√©rifiez que les policies RLS sont bien configur√©es');
            console.log('2. Ex√©cutez le script supabase_migration.sql qui configure les permissions');
        } else {
            console.error('\n‚ö†Ô∏è ERREUR INCONNUE:', error.message);
        }

        return { success: false, error };
    }

    console.log('‚úÖ Table user_scores accessible !');
    console.log('üìä Donn√©es de test:', data);

    // Test 3: Test d'insertion
    console.log('\nüìù Test d\'insertion...');

    const testData = {
        email: 'test@example.com',
        first_name: 'Test',
        last_name: 'User',
        job: 'Tester',
        total_score: 0,
        total_questions: 60,
        level_1_score: 0,
        level_2_score: 0,
        level_3_score: 0,
        answered_questions: [],
        unlocked_biases: [],
        all_levels_completed: false,
    };

    const { data: insertData, error: insertError } = await supabase
        .from('user_scores')
        .upsert(testData, { onConflict: 'email' })
        .select();

    if (insertError) {
        console.error('‚ùå Erreur lors de l\'insertion:', insertError);
        return { success: false, error: insertError };
    }

    console.log('‚úÖ Test d\'insertion r√©ussi !');
    console.log('üìä Donn√©es ins√©r√©es:', insertData);

    console.log('\n‚úÖ TOUS LES TESTS SONT PASS√âS !');
    console.log('üéâ Supabase est correctement configur√© !');

    return { success: true };
}

